$component: --mt-titulo-h5;

:host {
  #{$component}-text-color: var(--mt-color-titulo-h5-fonte);
  #{$component}-text-font-family: 'Roboto', sans-serif;
  #{$component}-text-font-weight: 800;
  #{$component}-text-text-transform: uppercase;
  #{$component}-text-font-size: 10px;

  #{$component}-line-color: var(#{$component}-text-color);
  #{$component}-line-font-size: 8px;
  #{$component}-line-margin: 0 4px 0 0;

  display: block;
}

.mt-titulo-h5 {
  &__container {
    display: flex;
    flex-direction: row;
    align-items: center;
  }

  &__line {
    color: var(#{$component}-line-color);
    font-size: var(#{$component}-line-font-size);
    margin: var(#{$component}-line-margin);
  }

  &__text {
    color: var(#{$component}-text-color);
    font-family: var(#{$component}-text-font-family);
    font-weight: var(#{$component}-text-font-weight);
    text-transform: var(#{$component}-text-text-transform);
    font-size: var(#{$component}-text-font-size);
    line-height: 1;
    margin: 0;
  }
}
