import { Component, h, Host, Prop } from '@stencil/core';
import { generateClasses } from '../../../global/typescript/functions';
import { MtAnos, MtEspecialidades, MtProdutos } from '../../../global/typescript/types';

@Component({
  tag: 'mt-titulo-h5',
  styleUrl: 'titulo-h5.scss',
  scoped: true,
})
export class TituloH5 {
  @Prop({ reflect: true }) produto: MtProdutos;

  @Prop({ reflect: true }) especialidade: MtEspecialidades;

  @Prop({ reflect: true }) ano: MtAnos;

  render() {
    const { produto, especialidade, ano } = this;

    return (
      <Host
        class={generateClasses(produto, especialidade, ano, {
          'mt-titulo-h5': true,
        })}
      >
        <div class="mt-titulo-h5__container">
          <div class="mt-titulo-h5__line">â–¶</div>

          <h5 class="mt-titulo-h5__text">
            <slot></slot>
          </h5>
        </div>
      </Host>
    );
  }
}
